import React from "react";
import DataTable from "react-data-table-component";
import Constants, { userToken } from "src/Constants";
import ActionButton from "./ActionButton";
import { CCard, CCardBody, CFormSwitch, CButton } from "@coreui/react";
import { ToastContainer, toast } from 'react-toastify';
import { useNavigate } from "react-router-dom";

const AllNotifications = () => {

    const navigate = useNavigate();

    const [data, setData] = React.useState([]);
    const [pagination, setPagination] = React.useState({
        per_page: 100,
        page_number: 1
    })
    const [load, setLoad] = React.useState(true)
    const [total, setTotal] = React.useState(0);
    const [filter, setFilter] = React.useState('');
    const inputRef = React.useRef()
    const handleChange = () => {
        setFilter(inputRef.current?.value ?? '')
    }
    const columns = [
        {
            name: 'User',
            selector: row => row.name
        },
        {
            name: 'Camera Display Name',
            selector: row => <>{(row.caution) ? <i className="fa-solid fa-exclamation-triangle text-warning"></i> : ""} {row.camera_name}</>
        },

        {
            name: 'Camera Address',
            selector: row => row.camera_address,
            sortable: true,
        },

        {
            name: 'No. of Expected Images',
            selector: row => row.expected_image,
            sortable: true,
        },

        {
            name: 'No. of Received Images',
            selector: row => row.get_image,
            sortable: true,
        },
        {
            name: 'No. of Hours',
            selector: row => row.hours,
            sortable: true,
        },
        {
            name: 'Time Range',
            selector: row => new Date(row.from).toLocaleString() + ' to ' + new Date(row.to).toLocaleString(),
            sortable: true,
        },
        {
            name: 'Status',
            selector: row => <CFormSwitch size="xl" onChange={() => handleChangeStatus(row.id)} checked={row.status} />,
            sortable: true,
        },

        {
            name: 'Action',
            selector: (row) => <ActionButton prefix="notification" onEdit={() => handleEdit(row)} onDelete={() => handleDelete(row.id)} onToggleStatus={() => handleChangeStatus(row.id)} />,
            sortable: true
        }
    ];

    const handleChangeStatus = async (id) => {
        let url = Constants.notificationStatus + id
        let response = await fetch(url, {
            headers: {
                Authorization: userToken()
            }
        })
        if (response.ok) {
            setLoad((p) => !p)
        }
    }

    const handleEdit = (row) => {
        navigate('/camera/notifications/edit', { state: row });
    }

    const handleDelete = async (id) => {
        let confirmation = window.confirm("Do you really want to delete this record?");
        if (!confirmation) {
            return;
        }
        let url = Constants.cameraNotificationDelete + id;
        let response = await fetch(url, {
            method: 'GET',
            headers: {
                'Authorization': userToken(),

            }
        })
        let json = await response.json();
        if (json.status === 200) {
            setLoad(!load);
            toast.success("Record deleted successfully!");
        }
    }


    React.useEffect(() => {
        let { sessionStorage } = window;
        if (sessionStorage.type && sessionStorage.message) {
            toast[sessionStorage.type](sessionStorage.message)
            sessionStorage.clear()
        }
    }, [])

    React.useEffect(() => {
        let url = Constants.cameraNotificationsAll;

        (async () => {
            let response = await fetch(url + `?per_page=${pagination.per_page}&page_number=${pagination.page_number}`, {
                method: 'POST',
                headers: {
                    'Authorization': userToken(),
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ filter })
            })
            let data = await response.json();
            console.log(data);
            setData(data.data
            );
            setTotal(data.totalResult);


        })()
    }, [pagination, load, filter])


    return (
        <>
            <ToastContainer
                position="top-right"
                theme="colored"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover />


            <CCard className="my-2">
                <CCardBody>
                    <div className="row">
                        <div className="col-md-6">
                            <h5 className="text-uppercase">ALL Notifications</h5>
                        </div>

                    </div>
                    <div className="row py-4">
                        <div className="col-6">
                            <input ref={inputRef} className="form-control" placeholder="Search..." />
                        </div>
                        <CButton color="primary" className="col-2" onClick={handleChange}>Search</CButton>
                    </div>
                </CCardBody>
            </CCard>

            <DataTable
                columns={columns}
                data={data}
                pagination
                onSelectedRowsChange={handleChange}
                paginationServer
                paginationPerPage={100}
                paginationRowsPerPageOptions={[100, 50, 30]}
                paginationTotalRows={total}
                className='camera-table'
                onChangeRowsPerPage={(per_page) => setPagination({ ...pagination, per_page })}
                onChangePage={(page_number, per_page) => setPagination({ ...pagination, page_number })}
            />
        </>
    )
}

export default AllNotifications;